@import url(https://fonts.googleapis.com/css?family=Montserrat:500);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* :global body {
  margin: 0px;
  padding: 0px;
} */

:global * {
  box-sizing: border-box;
  font-family: "robot", sans-serif;
}

.App {
  /* position: absolute; */
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background: #ebebf3;
  align-content: flex-end;
}

.sidebar{
  display: flex;
  flex-direction: column;
  /* align-items: stretch; */
  text-align: center;
  /* gap: 5px; */
  /* position: relative; */
}

.item-title{
  text-align: center;
  font-weight: 800;
  font-size: 20px;
  font-family: Montserrat, sans-serif;
  background-color: rgb(26, 89, 114);
  border-radius: 5px;
  padding: 5px;
  color: white;
  margin-top: 5px;
}

/* .wrapper{
  gap: 5px;
} */

.single-store{
  /* background-color: #fff; */
  /* border: rgb(7, 51, 71) 1px solid; */
  border-bottom: burlywood 1px solid;
  /* border-radius: 10px; */
  text-align: center;
  font-weight: 500;
  font-size: 16px;
  font-family: Montserrat, sans-serif;
  
}





.map-container{
    flex: 2 1;
}
.main{
    display: flex;
    margin-left: 5px;
    margin-right: 5px;
    grid-gap: 25px;
    gap: 25px;
    flex-wrap: wrap-reverse;
    /* position: relative; */
    width: 100%;
    /* align-items: stretch; */
}


.map-area{
    flex: 3 1;
    padding: 5px;
}

.sidebar-area{
    flex: 1 1;
    align-content: center;
    /* align-items: stretch; */
}



 .header{
    /* position: fixed; */
    display: flex;
    /* position: fixed; */
    /* top: 0px;
    left: 0px;
    right: 0px;
    z-index: var(--z-sticky); */
    justify-content: flex-end;
    align-items: center;
    padding-left: 2px;
}

li, a, button{
    font-family: Montserrat, sans-serif;
    font-weight: 500;
    font-size: 12px;
    color:  rgba(0,136,169,1);
    text-decoration: none;
}

.logo{
    cursor: pointer;
    margin-right: 2%;
    margin-left: 2%;
    font-family: Montserrat, sans-serif;
    color: darkgreen;
    font-size: 20px;
    font-weight: 900;
}

.dd-wrapper{
    flex-basis: 30%;
    margin-right: auto;
    width: -webkit-max-content;
    width: -moz-max-content;
    width: max-content; 
    color: darkgreen;
}

.nav_links{
    list-style: none;
}

.nav_links li{
    display: inline-block;
    padding: 0px 20px;
    color: darkgreen;

}

.nav_links li a{
    transition: all 0.3s ease 0s;
}

.nav_links li a:hover{
    color: lightgreen
}


